import React from 'react';
import {Link} from 'react-router-dom';
import DarkModeToggle from '../DarkModeToggle';
import useDarkMode from 'use-dark-mode';
import ArrowBackIosRoundedIcon from '@material-ui/icons/ArrowBackIosRounded';
import Typography from '@material-ui/core/Typography';
import img from '../../media/ChessProgramming.jpg';
import pin from '../../media/pin.png';
import '../../css/projects.scss';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});

// 5 columns
function createData(c1, c2, c3, c4, c5) {
  return { c1, c2, c3, c4, c5 };
}

function ShowLogo() {
  const darkMode = useDarkMode(false);
  return (
    <img
    id="covalent"
    src={
    darkMode.value ?
    "https://www.covalenthq.com/static/images/covalent-logo-white.png" :
    "https://www.covalenthq.com/static/images/covalent-logo.png"
    }
    />
  );
}

export default class onemillionwallets extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      address: '',
      isShowingTable: false,
      tableData: null
    };
    require('dotenv').config();
  }

  handleChange = (event) => {
    this.setState({address: event.target.value});
  }

  // we call the API on click
  handleClick = (event) => {
    this.setState({isShowingTable: true});
    const APIKEY = this.REACT_APP_API_KEY;
    const url = new URL(`https://api.covalenthq.com/v1/1/address/${this.state.address}/balances_v2`);
    url.search = new URLSearchParams({
        key: APIKEY
    })
    fetch(url)
    .then((resp) => resp.json())
    .then((data) => {
        let tokens = data.data.items;
        this.setState({tableData: tokens});
      }
    )
  }

  // return table data as array
  getTableData = () => {
    const data = this.state.tableData;
    if (data == undefined) return;
    var arr = [];
    var balance = 0;
    for (var i=0;i<data.length;i++) {
      if (data[i].contract_decimals > 0) {
        balance = parseInt(data[i].balance) / Math.pow(10, data[i].contract_decimals);
      } else {
          balance = parseInt(data[i].balance);
        }
      arr.push(createData(
      data[i].contract_name,
      data[i].contract_ticker_symbol,
      balance.toFixed(4),
      parseFloat(data[i].quote).toFixed(2),
      data[i].type
    ));
    }
    return arr;
  }

  TableComp = () => {
    const classes = useStyles();
    if (this.state.tableData == undefined) return <></>;
    if (!this.state.isShowingTable) return <></>;
    return (
      <>
      <TableContainer component={Paper}>
        <Table className={classes.table} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>Token</TableCell>
              <TableCell align="right">Symbol</TableCell>
              <TableCell align="right">Balance&nbsp;</TableCell>
              <TableCell align="right">Fiat Value&nbsp;</TableCell>
              <TableCell align="right">Type&nbsp;</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {this.getTableData().map((row) => (
              <TableRow key={row.c1}>
                <TableCell component="th" scope="row">
                  {row.c1}
                </TableCell>
                <TableCell align="right">{row.c2}</TableCell>
                <TableCell align="right">{row.c3}</TableCell>
                <TableCell align="right">{row.c4}</TableCell>
                <TableCell align="right">{row.c5}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer> <br/> <br/>
      </>
    )
  }

  render() {
    return (
      <>
        <div className="back">
          <Link to="../">
            <ArrowBackIosRoundedIcon color="secondary" />
          </Link>
        </div>
        <div className="project-body">
          <p className="project-header"> üí∞ My E-Wallet </p>
            <p className="date"> Jan. 4, 2021
            </p>
        Powered by
        <ShowLogo /><br/><br/>
        <input
        type="text"
        onChange={ this.handleChange }
        placeholder="Enter wallet address or ENS"
        /> &emsp;
        <input
          type="button"
          value="Get balance"
          onClick={this.handleClick}
        /> <br/> <br/>
        <this.TableComp/>
        </div>
      </>
    );
  }
}
