import React from 'react';
import { Link } from 'react-router-dom';
import DarkModeToggle from '../DarkModeToggle';
import useDarkMode from 'use-dark-mode';
import ArrowBackIosRoundedIcon from '@material-ui/icons/ArrowBackIosRounded';
import Typography from '@material-ui/core/Typography';
import img from '../../media/ChessProgramming.jpg';
import pin from '../../media/pin.png';
import '../../css/projects.scss';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});

// 5 columns on table
function createData(c1, c2, c3, c4, c5) {
  return { c1, c2, c3, c4, c5 };
}

function ShowLogo() {
  const darkMode = useDarkMode(false);
  return (
    <img
    id="covalent"
    src={
    darkMode.value ?
    "https://www.covalenthq.com/static/images/covalent-logo-white.png" :
    "https://www.covalenthq.com/static/images/covalent-logo.png"
    }
    />
  );
}

export default class onemillionwalletsLogs extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      address: "0xc0da01a04c3f3e0be433606045bb7017a7323e38",
      startBlock: '9601459',
      endBlock: '10005000',
      isShowingTable: false,
      tableData: null,
      isContractChosen: false,
      isTopicChosen: false,
      topic:"0x7d84a6263ae0d98d3329bd7b46bb4e8d6f98cd35a7adb45c274c8b7fd5ebd5e0",
      events: {}
    };
    this.api_url = 'https://api.covalenthq.com'
    require('dotenv').config();
  }
  handleAddrChange = (event) => {
    this.setState({address: event.target.value});
  }
  handleStartBlockChange = (event) => {
    this.setState({startBlock: event.target.value});
  }
  handleEndBlockChange = (event) => {
    this.setState({endBlock: event.target.value});
  }

  // we call the API on click
  handleClick = (event) => {
    this.setState({isShowingTable: true})
    if (this.state.isContractChosen) this.getLogsResponse_contract()
    else this.getLogsResponse_topic()
  }

  getLogsResponse_topic = () => {
    if (this.state.address == '' || this.state.startBlock == ''
    || this.state.endBlock == '') return;
    const endpoint = `/v1/1/events/topics/${this.state.topic}/`
    const url = new URL(this.api_url + endpoint);
    url.search = new URLSearchParams({
        key: this.REACT_APP_API_KEY,
        "starting-block": this.state.startBlock,
        "ending-block": this.state.endBlock
    })
    fetch(url)
    .then((resp) => resp.json())
    .then((data) => {
        let tokens = data.data.items;
        this.setState({tableData: tokens});
      }
    )
  }

  getLogsResponse_contract = () => {
    if (this.state.address == '' || this.state.startBlock == ''
    || this.state.endBlock == '') return;
    const endpoint = `/v1/1/events/address/${this.state.address}`
    const url = new URL(this.api_url + endpoint);
    url.search = new URLSearchParams({
        key: this.REACT_APP_API_KEY,
        "starting-block": this.state.startBlock,
        "ending-block": this.state.endBlock
    })
    fetch(url)
    .then((resp) => resp.json())
    .then((data) => {
        let tokens = data.data.items;
        this.setState({tableData: tokens});
      }
    )
  }

  // return table data as array
  getTableData = () => {
    const data = this.state.tableData;
    if (data == undefined) return;
    var arr = [];
    var balance = 0;
    for (var i=0;i<data.length;i++) {
      if (data[i].tx_hash != undefined &&
      data[i].decoded != undefined &&
      data[i].raw_log_topics != undefined &&
      data[i].sender_address != undefined &&
      data[i].block_signed_at != undefined) {
        arr.push(createData(
          data[i].tx_hash,
          data[i].decoded.signature,
          data[i].raw_log_topics,
          data[i].sender_address,
          data[i].block_signed_at
        ));
      }
    }
    return arr;
  }

  tableOff = () => {
    this.setState({isShowingTable: false})
  }

  filterByWeek = () => {
    this.setState({isShowingTable: false})
    var last_week = new Date();
    last_week.setDate(last_week.getDate()-7)
    for (var i=this.state.tableData.length-1;i>=0;i--) {
      var date = new Date(this.state.tableData[i].block_signed_at)
      if (date < last_week) {
        this.state.tableData.splice(i, 1)
      }
    }
    this.setState({isShowingTable: true})
  }

  // create a hash table of the # occurance of each element
  // (i.e events by volume)
  ExtraInfo = () => {
    var obj = {}
    for (var i=0;i<this.state.tableData.length;i++) {
      if (this.state.tableData[i].decoded != undefined) {
        var item = this.state.tableData[i].decoded.signature
        if (!(item in obj)) {
          obj[item] = 1
        }
        else obj[item]++
      }
    }
    this.state.events = obj
    return (
      <>
      Number of events: {this.getTableData().length} <br/><br/>
      -- Events by volume: -- <br/><br/>
      {Object.keys(this.state.events).map((key) =><> {key}: {this.state.events[key]} <br/></>)}
      <br/>
      </>
    )
  }

  // Show table (uses API response stored in this.state.tableData)
  TableComp = () => {
    const classes = useStyles();
    const data = this.getTableData();
    if (this.state.tableData == undefined) return <></>;
    if (!this.state.isShowingTable) return <></>;
    return (
      <>
      <input
        type="button"
        value="Clear table"
        onClick={this.tableOff}
        style={{color: "white",backgroundColor:"darkRed"}}
      /> &emsp;&emsp;
      <input
        type="button"
        value="Show only this week"
        onClick={this.filterByWeek}
        style={{color: "white",backgroundColor:"darkBlue"}}
      /> &emsp;&emsp; <br/><br/>
      <this.ExtraInfo/>
      <TableContainer component={Paper}>
        <Table className={classes.table} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>Transaction hash</TableCell>
              <TableCell align="right">Signature</TableCell>
              <TableCell align="right">Raw log topics&nbsp;</TableCell>
              <TableCell align="right">Sender&nbsp;</TableCell>
              <TableCell align="right">Timestamp&nbsp;</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {data.map((row) => (
              <TableRow key={row.c1}>
                <TableCell component="th" scope="row">
                  {row.c1}
                </TableCell>
                <TableCell align="right">{row.c2}</TableCell>
                <TableCell align="right">{row.c3}</TableCell>
                <TableCell align="right">{row.c4}</TableCell>
                <TableCell align="right">{row.c5}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer> <br/> <br/>
      </>
    )
  }

  showContractFields = () => {
    return (
      <>
      <input
      type="text"
      onChange={this.handleAddrChange}
      placeholder="Enter wallet address or ENS"
      /> &emsp;&emsp;
      <input
      type="text"
      onChange={this.handleStartBlockChange}
      placeholder="Enter starting block"
      /> &emsp;&emsp;
      <input
      type="text"
      onChange={this.handleEndBlockChange}
      placeholder="Enter ending block"
      /> &emsp;&emsp;
      <input
        type="button"
        value="Get logs"
        onClick={this.handleClick}
      /> <br/><br/>
      <this.TableComp/>
      </>
    )
  }

  showTopicFields = () => {
    return (
      <>
      <input
      type="text"
      onChange={ this.handleAddrChange }
      placeholder="Enter topic hash"
      /> &emsp;&emsp;
      <input
      type="text"
      onChange={ this.handleStartBlockChange }
      placeholder="Enter starting block"
      /> &emsp;&emsp;
      <input
      type="text"
      onChange={ this.handleEndBlockChange }
      placeholder="Enter ending block"
      /> &emsp;&emsp;
      <input
        type="button"
        value="Get logs"
        onClick={this.handleClick}
      /> <br/><br/>
      <this.TableComp/>
      </>
    )
  }

  MainComp = () => {
    if (!this.state.isContractChosen && !this.state.isTopicChosen) return <></>
    else if (this.state.isContractChosen) return this.showContractFields()
    else if (this.state.isTopicChosen) return this.showTopicFields()
    return <> </>
  }

  handleTopic = () => {
    this.setState({isContractChosen: false})
    this.setState({isTopicChosen: true})
  }
  handleContract = () => {
    this.setState({isContractChosen: true})
    this.setState({isTopicChosen: false})
  }

  render() {
    return (
      <>
        <div className="back">
          <Link to="../">
            <ArrowBackIosRoundedIcon color="secondary" />
          </Link>
        </div>
        <div className="project-body">
          <p className="project-header"> üî® Transaction Log Search Tool Using Covalent API </p>
            <p className="date"> Jan. 11, 2021
            </p>
        Powered by
        <ShowLogo /><br/><br/>
        <input
          type="button"
          value="Search by contract address"
          onClick={this.handleContract}
        /> &emsp;&emsp;
        <input
          type="button"
          value="Search by topic hash"
          onClick={this.handleTopic}
        /> <br/><br/>
        <this.MainComp/>
        </div>
      </>
    );
  }
}
